@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: franklin-gothic-compressed, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.state-map svg{
    max-width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    resize: both;
    margin: 0 auto;
}
/* .state-map svg{
    max-width: 100%;
} */

/* .state-map path{
    fill: #c4c0bc;
} */

.state-map path:hover{
    fill: #1ca2ab;
}
.legend-wrapper{
    padding: 0px 10px;
    width:100vw;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.legend-totalAcre{
    /* padding: 5px; */
    height: 60px;
}

.legend-totalAcre__label{
 font-size: 12px;
 font-weight: bold;
}

.legend-totalAcre__number{
    font-family: ff-tisa-web-pro, serif;
    font-style: normal;
    font-weight: 900;
    background-color: #4a2e1c;
    padding-top: 5px;
    padding-bottom: 6px;
    color: white;
    text-align: center;
}



/* -- legend color information -- */

.legend-colorInfo{
    padding-left: 20px;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: space-evenly;
    height: 60px;
}

.display-flex{
    display: flex;
    align-items: center;
}

.legend-colorInfo__label{
    font-size: 10px;
    font-weight: bold;
    padding-left: 5px;
}

.legend-colorInfo__box{
    width: 13px;
    height: 13px;
}

.legend-colorInfo__color__hasAcres{
    background-color:#4a2e1c;
}

.legend-colorInfo__box__selected{
    background-color: #1ca2ab;
}

.legend-colorInfo__color__noAcres{
    background-color: #c4c0bc;
}


.StateDropdownBtn-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-right: 25px;
    padding-top: 10px;
    align-items: flex-end;
}
select{
    -webkit-appearance: none;
            appearance: none;
    display: flex;
    flex-direction: row;
    background-color: #e26f27;
    border: none;
    padding: 10px;
    border-radius: 15px;
    cursor: inherit;
    color: white;
    font-size: 18px;
    font-weight: 600;
    text-align: center;
    width: 150px;
}
select::after {
    content: "";
    width: 0.8em;
    height: 0.5em;
    background-color: white;
    -webkit-clip-path: polygon(100% 0%, 0 0%, 50% 100%);
            clip-path: polygon(100% 0%, 0 0%, 50% 100%);
  }
